*,
*::after,
*::before {
	-webkit-box-sizing: border-box;
	        box-sizing: border-box;
}

body {
	font-family: "Franklin Gothic Medium", "Helvetica Neue", Helvetica, Arial, sans-serif;
	min-height: 100vh;
	overflow: hidden;
	background:  white; /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
	background-color: var(--color-bg);
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
}

/* Color schemes */
.demo-1 {
	--color-text: #fff;
	--color-bg: white;
	--color-link: #fff;
	--color-link-hover: #fff473;
	--color-nav: #fff;
	background-color: #c2ceda !important;
	--color-bg-slide-1: linear-gradient(to right, #232526, #414345);
	display: -ms-flexbox;
	display: flex;
	-ms-flex-pack: center;
	    justify-content: center;
	-ms-flex-align: center;
	    align-items: center;
	width: 100vw;
	height: 100vh;
	color: #fff;
	display: flex;
    -ms-flex-direction: row;
        flex-direction: row;
}
.info, .nurse{
	width: 500px;
	background-color: white;
	padding: 10px 10px;
	margin: 10px 10px;
	border-radius: 5px;
	-webkit-box-shadow: 0 3px 6px rgba(0, 0, 0, 0.16), 0 3px 6px rgba(0, 0, 0, 0.23);
	        box-shadow: 0 3px 6px rgba(0, 0, 0, 0.16), 0 3px 6px rgba(0, 0, 0, 0.23);
}

.info > button{
	width: 100%;
}

.info-wrapper{
	display: -ms-flexbox;
	display: flex;
	-ms-flex-direction: column;
	    flex-direction: column;
	-ms-flex-align: center;
	    align-items: center;
}

.nurse > button{
	width: 100%;
}

a {
	text-decoration: none;
	color: var(--color-link);
	outline: none;
}

a:hover,
a:focus {
	color: var(--color-link-hover);
	outline: none;
}

.button {
  margin-top: 10px;
  position: relative;
  -webkit-appearance: none;
     -moz-appearance: none;
          appearance: none;
  border: #c2ceda  2px solid ;
  background-color: white;
  color: black ;
  font-weight: bold;
  padding: 1em 2em;
  font-size: 1.2em;
  cursor: pointer;
  outline: none;
  overflow: hidden;
  border-radius: 100px;
}
.button span {
  position: relative;
  pointer-events: none;
}
.button::before {
  --size: 0;
  content: '';
  position: absolute;
  left: var(--x);
  top: var(--y);
  width: var(--size);
  height: var(--size);
  background: -webkit-radial-gradient(circle closest-side, #dcf1dc, rgba(0, 0, 0, 0));
  background: -o-radial-gradient(circle closest-side, #dcf1dc, rgba(0, 0, 0, 0));
  background: radial-gradient(circle closest-side, #dcf1dc, rgba(0, 0, 0, 0));
  -webkit-transform: translate(-50%, -50%);
      -ms-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  -webkit-transition: width .2s ease, height .2s ease;
  -o-transition: width .2s ease, height .2s ease;
  transition: width .2s ease, height .2s ease;
}
.button:hover::before {
  --size: 400px;
}

.hospital-icon, .nurse-icon{
	width: 150px;
	height: 150px;
}

.icon{
	width: 200px;
}


